@import url(https://fonts.googleapis.com/css?family=Pacifico);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAFAFA;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 950px;
  margin: 0 auto;
}

.searchBar {
    position: fixed;
    width: 100%;
    display: flex;
    height: 70px;
    align-items: center;
    top: 0px;
    background-color: white;
    border-bottom: 1px solid lightgrey;
}

.searchBarContainer {
    width: 950px;
    margin: 0 auto;
    display: flex;
    align-items: center;
}

h1 {
    font-family: 'Pacifico', cursive;
    font-weight: lighter;
}

.searchFormDiv {
    width: 100%;
    display: flex;
    justify-content: center;
}

.searchBarIcons {
    display: flex;
    justify-content: space-between;
    width: 20%;
}

.instaclone {
    border: none;
    background: none;
}

.instaclone:hover {
    cursor: pointer;
}

input {
    height: 20px;
    text-align: center;
    width: 200px;
    border: 1px solid lightgray;
    border-radius: 5px;
}
.commentSection {
    max-width: 600px;
}

.comments {
    width: 100%;
    margin: 10px;
}

.commentUsername {
    margin-right: 5px;
    font-weight: bold;
}

.commentForm {
    margin-left: 10px;
}

.commentForm input {
    width: 100%;
    height: 40px;
    border: none;
    text-align: left;
    font-size: 18px;
}

hr {
    margin-left: 30px;
    border-color: white;
}
.postContainer {
    width: 640px;
}

.outerPostContainer {
    width: 950px;
    margin: 0 auto;
}

.post {
    width: 100%;
    border: 1px solid lightgrey;
    border-radius: 5px;
    margin: 100px 0;
    background-color: white;
}

.header {
    display: flex;
    margin-left: 20px;
    align-items: center;
}

.thumbnail {
    width: 30px;
    border: 1px solid white;
    border-radius: 17px;
}

.username {
    font-weight: bold;
    margin-left: 10px;
}

.likes {
    font-weight: 500;
    margin: 10px;
}

.footerIcons {
    margin: 10px;
    width: 8%;
    display: flex;
    justify-content: space-between;
}

.likeButton {
    border: none;
    background-color: white;
}
.loginForm {
    display: flex;
    flex-direction: column;
}

.loginScreen {
    margin: 0 auto;
    width: 50%;
    text-align: center;
}
